@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap);
.alert {
  bottom: 0;
  left: 50%;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 24px;
  z-index: 1000;
  position: fixed;
  font-size: 14px;
  min-width: 200px;
  border-radius: 24px;
  transition: all 0.3s ease;
  background: var(--primary-bg);
  padding: 10px 12px 10px 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.alert.alert--active {
  transform: translate(calc(-50% - 24px), -80%);
}

.alert.alert--inactive {
  transform: translate(calc(-50% - 24px), 150%);
}

.alert__message {
  color: var(--text-color-primary);
}

.alert__icon {
  width: 24px;
  height: 24px;
  padding: 8px;
  display: flex;
  cursor: pointer;
  margin-left: 16px;
  border-radius: 24px;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease;
}
.alert__icon:hover {
  background: var(--white);
}

.close__icon {
  width: 16px;
  height: 16px;
}

.users__wrapper {
  padding: 24px;
  border-radius: 24px;
  background: var(--white);
  height: calc(100% - 48px);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.users__heading {
  font-size: 24px;
  font-weight: 500;
  color: var(--text-color-primary);
}

.user__wrapper {
  padding-top: 12px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.user__name {
  font-size: 16px;
  padding: 0px 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: calc(100% - 68px);
  color: var(--text-color-primary);
}

.avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 50%;
  background: var(--white);
  color: var(--text-color-primary);
}

.avatar.avatar--bg {
  background: var(--primary-bg);
}

.button {
  border: none;
  outline: none;
  min-width: 98px;
  cursor: pointer;
  color: #ffffff;
  font-size: 16px;
  min-height: 48px;
  padding: 12px 16px;
  border-radius: 24px;
  background: var(--primary);
  transition: box-shadow 0.3s ease;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.button.button--block {
  width: 100%;
}

.button:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.message__wrapper {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}
.message__wrapper--right {
  justify-content: flex-end;
}

.message__container {
  max-width: 50%;
  margin-top: 24px;
  font-size: 14px;
  padding: 12px 20px;
  background: var(--white);
  color: var(--text-color-primary);
  border-radius: 24px 24px 24px 0px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.message__container--right {
  color: #ffffff;
  text-align: right;
  background: var(--primary);
  border-radius: 24px 24px 0px 24px;
}

.message__bot {
  width: 100%;
  font-size: 16px;
  padding-top: 24px;
  margin-right: 5vw;
  text-align: center;
  color: var(--text-color-secondary);
}

.message__text {
  font-size: 16px;
  word-break: break-all;
}

.message__text__hint {
  margin-top: 10px;
  margin-left: 10px;
}

.message__timestamp {
  font-size: 10px;
  color: var(--text-color-secondary);
}

.message__timestamp--right {
  text-align: right;
  color: #dadada;
}

.input__wrapper {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  min-height: 36px;
  border-radius: 24px;
  background: var(--white);
  padding: 8px 14px 8px 18px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.suggestions__wrapper {
  -webkit-appearance: none;
          appearance: none; /* Removes default appearance */
  border: none; /* Removes border */
  background-color: transparent; /* Makes background transparent */
  padding: 0; /* Removes default padding */
  font-size: 16px; /* Adjust font size */
  cursor: pointer; /* Shows cursor as pointer when hovering */
  outline: none; /* Removes focus outline */
  
  /* Italicize text */
  font-style: italic;
  
  /* Apply slight blur */
  filter: blur(0.5px);
}

.suggestions__wrapper option {
  /* Styles for each option if needed */
  padding: 8px 12px; /* Example padding */
  background-color: white; /* Example background color */
}

/* Example styling for selected option */
.suggestions__wrapper option:checked {
  background-color: lightblue; /* Example selected background color */
}


.suggestions {
    position: absolute;
    list-style-type: none;
    background-color: var(--primary-bg);
    text-align: left;
    line-height: 30px;
    width: 20%;
    border-radius: 20px;
    padding-left: 15px;
}

.suggestions li:hover {
    padding: 5px;
    cursor: pointer;
    color: blueviolet;
}

.input__use__model{
  position: relative;
  font-style: italic;
  font-weight: bold;
  background-color: var(--primary-bg);
  color: var(--primary);
  padding: 10px;
  border-radius: 30px;
  margin-right: 10px;
  min-width: -webkit-max-content;
  min-width: max-content;
}

.input__use__model .input__use__model__close{
  font-size: 15px;
  font-weight: bold;
  color: red;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
}

.input__send {
  width: 24px;
  height: 24px;
  display: flex;
  cursor: pointer;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  padding: 10px 11px 10px 9px;
  transition: background 0.3s ease;
}
.input__send:hover {
  background: var(--primary-bg);
}

.send_icon {
  width: 24px;
  height: 24px;
}

.input {
  width: 100%;
  border: none;
  outline: none;
  font-size: 16px;
  background: transparent;
  color: var(--text-color-primary);
}
.input--shrunk {
  width: calc(100% - 42px);
}

.messages__wrapper {
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.messages__container::-webkit-scrollbar {
  display: none;
}

.messages__container {
  display: flex;
  overflow-y: auto;
  padding-bottom: 24px;
  flex-direction: column;
  max-height: calc(100% - 60px - 24px);
}

.messages__wrapper {
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.messages__container::-webkit-scrollbar {
  display: none;
}

.messages__container {
  display: flex;
  overflow-y: auto;
  padding-bottom: 24px;
  flex-direction: column;
  max-height: calc(100% - 60px - 24px);
}

.chat__wrapper {
  background: var(--primary);
  height: calc(100vh - 48px);
  padding: 24px;
}

.chat__container {
  margin: 0 auto;
  padding-top: 24px;
  max-width: 100vw;
  border-radius: 24px;
  height: calc(100% - 48px);
  background: var(--primary-bg);
}

.chat__header {
  margin: 0px 24px;
  display: flex;
  padding: 12px 24px;
  border-radius: 24px;
  align-items: center;
  background: var(--white);
  justify-content: space-between;
  width: calc(100% - 48px - 48px);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.chat__name {
  font-size: 28px;
  font-weight: 600;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: var(--text-color-primary);
}

.chat__actions {
  display: flex;
  align-items: center;
  justify-content: center;
}

.chat__menu,
.chat__theme {
  width: 24px;
  height: 24px;
  display: flex;
  padding: 10px;
  cursor: pointer;
  border-radius: 50%;
  margin-right: 12px;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease;
}
.chat__theme:hover,
.chat__menu:hover {
  background: var(--primary-bg);
}

.chat__menu {
  padding: 10px;
  display: none;
  margin-right: 8px;
}
.chat__menu img {
  width: 36px;
  height: 36px;
}

.chat__body {
  display: flex;
  align-items: flex-start;
  width: calc(100% + 24px);
  height: calc(100% - 48px - 24px);
  justify-content: flex-start;
}

.chat__users,
.chat__messages {
  margin-left: 5%;
  padding: 24px;
  overflow-y: auto;
  border-radius: 24px;
  height: calc(100% - 48px);
}

.chat__users {
  width: 280px;
}

.chat__messages {
  right: 24px;
  position: relative;
  width: calc(100% - 280px);
}

.chat__connection-close {
  height: 15px;
  width: 15px;
  margin-right: 10px;
  background-color: darkred;
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
}

.chat__connection-open {
  height: 15px;
  width: 15px;
  margin-right: 10px;
  background-color: forestgreen;
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
}

@media (max-width: 640px) {
  .chat__users {
    padding: 24px;
    position: absolute;
    visibility: hidden;
    width: calc(100% - 48px);
  }
  .chat__users.chat__users--show {
    visibility: visible;
  }
  .chat__body {
    width: 100%;
    position: relative;
  }
  .chat__messages {
    width: 100%;
    position: static;
  }
  .chat__header {
    padding-left: 12px;
    width: calc(100% - 48px - 36px);
  }
  .chat__menu {
    display: flex;
  }

  .chat__menu:hover {
    background: var(--primary-bg);
  }
}

.join__wrapper {
  height: 100%;
  margin: 0 auto;
  display: flex;
  padding: 0px 24px;
  align-items: center;
  justify-content: center;
  background: var(--primary);
}

.join__container {
  width: 300px;
  position: relative;
  padding: 28px 24px;
  border-radius: 32px;
  background: var(--primary-bg);
}

.join__info,
.join__description {
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  background: var(--primary-bg);
  color: var(--text-color-primary);
}

.join__info {
  bottom: -50px;
  width: 32px;
  height: 32px;
  display: flex;
  font-size: 16px;
  cursor: pointer;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
}
.join__description {
  opacity: 0;
  bottom: -50%;
  width: 240px;
  font-size: 13px;
  padding: 16px 8px;
  text-align: center;
  border-radius: 24px;
}

.join__info:hover + .join__description {
  opacity: 1;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
  -webkit-tap-highlight-color: transparent;
}

.app {
  background: var(--primary-bg);
  height: 100vh;
  width: auto;
}

::placeholder {
  color: var(--text-color-disabled);
}

